"use client";

import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import Image from "next/image";
import { Search, Bell, User, X } from "lucide-react";
import { UserButton, SignedIn, SignedOut, SignInButton } from "@clerk/nextjs";
import clsx from "clsx";
import { usePathname, useRouter } from "next/navigation";
import { motion, AnimatePresence } from "framer-motion";
import { searchMovies } from "@/api/tmdb";

const NAV_LINKS = [
    { name: "Movies", href: "/movies" },
    { name: "TV Shows", href: "/tv" },
    { name: "Discover", href: "/discover" },
    { name: "Lists", href: "/lists" },
];

export function Navbar() {
    const [isScrolled, setIsScrolled] = useState(false);
    const [showSearch, setShowSearch] = useState(false);
    const [searchQuery, setSearchQuery] = useState("");
    const [suggestions, setSuggestions] = useState<any[]>([]);
    const wrapperRef = useRef<HTMLDivElement>(null);

    const pathname = usePathname();
    const router = useRouter();

    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 0);
        };
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    // Fetch suggestions when query changes
    useEffect(() => {
        const fetchSuggestions = async () => {
            if (searchQuery.length > 2) {
                const data = await searchMovies(searchQuery);
                if (data?.results) {
                    setSuggestions(data.results.slice(0, 5));
                }
            } else {
                setSuggestions([]);
            }
        };

        const timeoutId = setTimeout(fetchSuggestions, 300); // Debounce
        return () => clearTimeout(timeoutId);
    }, [searchQuery]);

    // Close on click outside
    useEffect(() => {
        function handleClickOutside(event: MouseEvent) {
            if (wrapperRef.current && !wrapperRef.current.contains(event.target as Node)) {
                setSuggestions([]);
            }
        }
        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    }, [wrapperRef]);

    const handleSearchCheck = (e: React.FormEvent) => {
        e.preventDefault();
        if (searchQuery.trim()) {
            router.push(`/search?q=${encodeURIComponent(searchQuery)}`);
            setShowSearch(false);
            setSuggestions([]);
        }
    };

    return (
        <nav
            className={clsx(
                "fixed top-0 left-0 right-0 z-50 transition-all duration-300 border-b",
                isScrolled
                    ? "bg-black/80 backdrop-blur-xl border-white/5 py-4"
                    : "bg-transparent border-transparent py-6"
            )}
        >
            <div className="container mx-auto px-6 lg:px-20 flex items-center justify-between">
                {/* Logo */}
                <Link href="/" className="flex items-center gap-2 relative z-50">
                    <Image src="/movie.png" alt="TheMovie Logo" width={40} height={40} className="w-10 h-10 object-contain" />
                    <span className="font-display font-bold text-2xl tracking-tight text-white hidden sm:block">
                        The<span className="text-accent-primary">Movie</span>
                    </span>
                </Link>

                {/* Desktop Navigation */}
                <div className="hidden md:flex items-center gap-8 absolute left-1/2 transform -translate-x-1/2">
                    {NAV_LINKS.map((link) => {
                        const isActive = pathname === link.href;
                        return (
                            <Link
                                key={link.name}
                                href={link.href}
                                className={clsx(
                                    "text-sm font-medium transition-all relative group",
                                    isActive ? "text-white" : "text-text-secondary hover:text-white"
                                )}
                            >
                                {link.name}
                                <span className={clsx(
                                    "absolute -bottom-1 left-0 w-full h-[2px] bg-accent-primary rounded-full transition-all duration-300",
                                    isActive ? "opacity-100 scale-x-100" : "opacity-0 scale-x-0 group-hover:opacity-50 group-hover:scale-x-50"
                                )} />
                            </Link>
                        );
                    })}
                </div>

                {/* Actions */}
                <div className="flex items-center gap-4">
                    {/* Search Bar */}
                    <div ref={wrapperRef} className="relative flex items-center">
                        <AnimatePresence>
                            {showSearch && (
                                <motion.form
                                    initial={{ width: 0, opacity: 0 }}
                                    animate={{ width: 240, opacity: 1 }}
                                    exit={{ width: 0, opacity: 0 }}
                                    onSubmit={handleSearchCheck}
                                    className="overflow-visible mr-2 relative"
                                >
                                    <input
                                        type="text"
                                        placeholder="Search..."
                                        className="w-full bg-white/10 border border-white/10 rounded-full px-4 py-1.5 text-sm text-white focus:outline-none focus:border-accent-primary"
                                        autoFocus
                                        value={searchQuery}
                                        onChange={(e) => setSearchQuery(e.target.value)}
                                    />
                                    {/* Suggestions Dropdown */}
                                    {suggestions.length > 0 && (
                                        <div className="absolute top-full text-start left-0 w-96 mt-2 bg-bg-card border border-white/10 rounded-xl overflow-hidden shadow-2xl z-50 max-h-[60vh] overflow-y-auto">
                                            {suggestions.map((movie) => (
                                                <Link
                                                    key={movie.id}
                                                    href={`/movie/${movie.id}`}
                                                    onClick={() => {
                                                        setShowSearch(false);
                                                        setSuggestions([]);
                                                        setSearchQuery("");
                                                    }}
                                                    className="flex items-start gap-4 p-4 hover:bg-white/5 transition-colors border-b border-white/5 last:border-0"
                                                >
                                                    <div className="w-12 h-16 relative bg-neutral-800 rounded overflow-hidden shrink-0">
                                                        {movie.poster_path && (
                                                            <Image
                                                                src={`https://image.tmdb.org/t/p/w92${movie.poster_path}`}
                                                                alt={movie.title}
                                                                fill
                                                                className="object-cover"
                                                            />
                                                        )}
                                                    </div>
                                                    <div className="flex-1 min-w-0">
                                                        <p className="text-sm font-bold text-white leading-tight mb-1">{movie.title}</p>
                                                        <p className="text-xs text-text-muted">{movie.release_date?.split("-")[0]}</p>
                                                    </div>
                                                </Link>
                                            ))}
                                        </div>
                                    )}
                                </motion.form>
                            )}
                        </AnimatePresence>
                        <button
                            onClick={() => setShowSearch(!showSearch)}
                            className="text-text-secondary hover:text-white transition-colors p-2"
                        >
                            {showSearch ? <X size={20} /> : <Search size={20} />}
                        </button>
                    </div>

                    <SignedIn>
                        <Link href="/profile">
                            <button className="text-text-secondary hover:text-white transition-colors p-2">
                                <User size={20} />
                            </button>
                        </Link>
                        <UserButton
                            appearance={{
                                elements: {
                                    avatarBox: "w-9 h-9 border-2 border-white/10 hover:border-text-primary transition-colors"
                                }
                            }}
                        />
                    </SignedIn>
                    <SignedOut>
                        <SignInButton mode="modal">
                            <button className="px-5 py-2 bg-accent-primary hover:bg-accent-primary/90 text-white rounded-lg text-sm font-bold transition-all shadow-lg shadow-accent-primary/20">
                                Sign In
                            </button>
                        </SignInButton>
                    </SignedOut>
                </div>
            </div>
        </nav>
    );
}
